	module.exports = {"data":{"wordpressPost":{"title":"Anna Heinrich On Life With The Bachelor Tim Robards","content":"<p><a href=\"https://www.instagram.com/annaheinrich1/\">Reality TV star Anna Heinrich</a> is quite something. You can sort of see it in pictures but it&#8217;s nothing when compared to the woman in the flesh. In person she literally glows like she&#8217;s lit from within and we swear, her smile is so intense it will stop you in your tracks.</p>\n<p>But there&#8217;s another reason for all that loveliness. The former contestant on The Bachelor has been floating on cloud nine since winning the series and taking home the major prize &#8211; <a href=\"https://www.instagram.com/mrtimrobards/\">Tim Robards</a>. And rightly so. Because the plain and simple truth is she&#8217;s deeply in love.</p>\n<p>While plenty of reality TV romances have exploded into flames and dwindled into tabloid ashes, Anna, 30, and Tim, 34, are the real deal. They&#8217;re rarely apart and are regularly spotted walking (hand in hand &#8211; obvs) in their home suburb of Bondi, Sydney.</p>\n<figure id=\"attachment_1971\" style=\"width: 239px\" class=\"wp-caption alignleft\"><img class=\"size-medium wp-image-1971\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/12215341/bikini-jp-239x300.jpg\" alt=\"\" width=\"239\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/12215341/bikini-jp-239x300.jpg 239w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/12215341/bikini-jp.jpg 475w\" sizes=\"(max-width: 239px) 100vw, 239px\" /><figcaption class=\"wp-caption-text\">Image: Instagram/@annaheinrich1</figcaption></figure>\n<p>Anna, an Oral-B Toothpaste ambassador, regular posts pictures of the two together &#8211; sometimes mugging for the camera, occasionally kissing, but always glowing with happiness. On that glow, Anna does have a few tips for those of us who, well, don&#8217;t have a Bachelor up our sleeves.</p>\n<p>&#8220;Drink lots of water and get lots of sleep,&#8221; she says.</p>\n<p>And she recommends exercise. Anna runs. She lifts weights. She swims, she boxes. In fact, she&#8217;s pretty happy to work up a sweat in any way, shape or form. And that helps her to feel positive and yes, pretty too.</p>\n<p>&#8220;I feel beautiful and strong after a workout,&#8221; she says.</p>\n<p>&#8220;When I finished the Nike Half Marathon &#8230; that was a real moment. I never thought I&#8217;d be able to do that, but  hard work pays off &#8211; it just does. I felt beautiful that day.&#8221;</p>\n<figure id=\"attachment_1972\" style=\"width: 239px\" class=\"wp-caption alignleft\"><img class=\"size-medium wp-image-1972\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/12215551/Lift-logies-JP-239x300.jpg\" alt=\"\" width=\"239\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/12215551/Lift-logies-JP-239x300.jpg 239w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/12215551/Lift-logies-JP.jpg 475w\" sizes=\"(max-width: 239px) 100vw, 239px\" /><figcaption class=\"wp-caption-text\">Image: Instagram/@annaheinrich1</figcaption></figure>\n<p><strong><em> </em></strong>Anna is also firm in her belief that being positive and kind too will make you feel and even look more lovely.</p>\n<p>&#8220;I do believe that we should all be good people and support one another.&#8221;</p>\n<p>Finally, she advises the following never-fail beauty tip and, if you ask us, it may be the most meaningful and the most effective too:</p>\n<p>&#8220;Be genuinely happy. And always be the best version of yourself. I always try. I don&#8217;t always hit the mark, but I try.&#8221;</p>\n<p>Of course, having a devastatingly handsome, clever, funny man completely devoted to you will help too. Just saying. On that, Anna agrees.</p>\n<p>&#8220;Nothing makes me feel more beautiful than when he looks at me,&#8221; she says.</p>\n<p>&#8220;I still get butterflies.&#8221;</p>\n<figure id=\"attachment_1973\" style=\"width: 298px\" class=\"wp-caption alignleft\"><img class=\"size-medium wp-image-1973\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/12215808/Ok-jpg-298x300.jpg\" alt=\"\" width=\"298\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/12215808/Ok-jpg-298x300.jpg 298w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/12215808/Ok-jpg-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/12215808/Ok-jpg.jpg 590w\" sizes=\"(max-width: 298px) 100vw, 298px\" /><figcaption class=\"wp-caption-text\">Image: Instagram/@annaheinrich1</figcaption></figure>\n<p><strong><em>                                                                        <img class=\"size-medium wp-image-1980 alignright\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/12221934/Anna-Logies-jpg-267x300.jpg\" alt=\"\" width=\"267\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/12221934/Anna-Logies-jpg-267x300.jpg 267w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/12221934/Anna-Logies-jpg.jpg 528w\" sizes=\"(max-width: 267px) 100vw, 267px\" /></em></strong></p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/12213346/Beach-Anna-jpg.jpg"},"categories":[{"slug":"beauty-talk","name":"Beauty Talk"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"1f44f83b-5501-58bb-bf4e-92aef2b06f7c"}}

